import Vue from 'vue';
import axios from 'axios';
import App from './App.vue';
import router from './router.js';
import {
    Button,
    Menu,
    Submenu,
    MenuItem,
    MenuItemGroup,
    Row,
    Col,
    Badge,
    Tabs,
    TabPane,
    Tag,
    Dialog,
    Input,
    Message
} from 'element-ui';
import qs from 'qs';
import Raven from 'raven-js';
import RavenVue from 'raven-js/plugins/vue';



Vue.use(Button);
Vue.use(Menu);
Vue.use(Submenu);
Vue.use(MenuItem);
Vue.use(MenuItemGroup);
Vue.use(Row);
Vue.use(Col);
Vue.use(Badge);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Tag);
Vue.use(Dialog);
Vue.use(Input);
Vue.prototype.$message = Message;
Vue.prototype.$ajax = axios;
Vue.prototype.$qs = qs;
Vue.prototype.$help = new Vue(); // 

// 
Vue.filter('notNull', function (value) {
    if (value == null) {
        return 0;
    }
    else {
        return value;
    }
});
Raven
    .config('https://19e3e1eddff44c439bd930b7e6b71475@sentry.io/1199489')
    .addPlugin(RavenVue, Vue)
    .install();
new Vue({
    router,
    el: '#app',
    render: h => h(App)
});



